import React, { useEffect, useRef, lazy } from 'react'
import { PostTrimContent, themeImpressionTracking } from "../../../../utils";
import { isTagShop, isTagbox } from "../../../../actions/api";
import AuthorDetails from "./AuthorDetails";
import Media from "./Media";
import PostContent from "../../Elements/PostContent/PostContent";
import SocialAction from "../../Elements/SocialAction/SocialAction";
import ShareOnHover from "../../Elements/ShareOnHover/ShareOnHover";
import CTAButton from "../../Elements/CTA/CtaButton";
import Rating from "../../Elements/Rating/Rating";

const trim_content_length = 200;

const Card = ({ adjustWidth, clickToShowPopUp, index, onClickToCTA, onClickPopUpSlider, ThemeStyle, CardStyle, Post, ThemeInfo, counter }) => {
    const postTrack = useRef(null);
    const themeId = ThemeInfo.id;

    const cardSize = { width: `${adjustWidth}%`, padding: CardStyle.style.padding / 2};
    const mediaType = Post.type !== 1;
    const rating = Post.rating > 0;
    const contentClass = Post.rating > 0 ? "tb_nc_rating_content" : "tb_nc_content";
    const textDecorate = Post.type === 1 && CardStyle.style.textDecoration ? "tb_nc_text_decoration tb_nc_text_post" : "";
    const postTextContent = CardStyle.content.trimcontent ? PostTrimContent(Post.content.text, trim_content_length) : Post.content.text;

    useEffect(() => {
      if (isTagShop) themeImpressionTracking(postTrack);
    }, []);

    return (
      <div style={cardSize} className={`tb_nc_post_wrapper ${(isTagbox && themeId == 83) ? `tb_nc_post_gradient tb_nc_gradient-${counter}`: "" }`}>
          <div id={`tb_nc_id-${Post.id}`} tb-network={Post.networkId} post-id={Post.referenceId || Post.id} feed-id={Post.feedId} ref={postTrack} tabIndex="0" area-label={postTextContent} role="article">
            <div className="tb_nc_post_wrap_in">
              <div className="tb_nc_post_in" onClick={clickToShowPopUp(index, Post)} role="button" aria-label="open detail modal for post" tabIndex="0">
                <div className="tb_nc_post_media_wrapp" role="button" tabIndex="0" aria-label="Pop Up Button">
                  {Post.share.status ? (
                      <ShareOnHover share={Post.share} shareClass={"tb_nc_share_container"} Post={Post} ThemeInfo={ThemeInfo} />
                  ) : null}
                  {mediaType ? (
                    <Media Post={Post} CardStyle={CardStyle} ThemeStyle={ThemeStyle} ThemeInfo={ThemeInfo} index={index} />
                  ) : null}
                  {rating  && themeId != 59 && !isTagShop ? (
                    <div className="tb_nc_rating__">
                      <Rating rating={Post.rating} networkId={Post.networkId} />
                    </div>
                  ) : null}
                </div>
                <div className="tb_nc_contant_wrapper">
                <AuthorDetails Post={Post} CardStyle={CardStyle} ThemeStyle={ThemeStyle} mediaType={mediaType} ThemeInfo={ThemeInfo}/>
                  { Post.ctaStatus ?
                      <div className="tb_nc_post_cta">
                        <CTAButton ctaClass={"tb_nc_post_cta_btn"} cta={Post} Post={Post} onClickToCTA={onClickToCTA} />
                      </div>
                  : null }
                  {rating && isTagShop ? (
                      <div className="tb_nc_rating__">
                        <Rating rating={Post.rating} networkId={Post.networkId} />
                      </div>
                    ) : null}
                  {((CardStyle.content.status) || Post.type === 1) ? (
                      <PostContent contentClass={`${contentClass} ${textDecorate}`} Post={Post} content={postTextContent} CardStyle={CardStyle} contentTitle={Post.content.title} /> 
                  ) : null}
                
                </div>
                {CardStyle.actionStatus ? (
                    <SocialAction Post={Post} CardStyle={CardStyle} actionClass={`tb_nc_social_action__`}/>
                ) : null}
              </div>
            </div>
          </div>
      </div>
    );
}
export default Card